.cart_sum {
    width : 82%;
    box-sizing : border-box;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 130px;
}

.cart_sum div {
    display: flex;
    flex-direction: column;
    width: 90%;
}

.cart_sum div p {
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-bottom: 20px;
    padding: 20px;
    font-weight: 500;
    font-size: 25px;
    text-align: start;
    background: #f1eadc;
}

@media (min-width: 510px) {
    .cart_sum div {
        flex-direction: row;
        justify-content: flex-end;
        width: 100%;
    }

    .cart_sum div p {
        display: flex;
        justify-content: flex-start;
        margin-right: 30px;
        margin-bottom: 0;
        padding: 20px 50px;
    }

    .cart_sum div p span {
        margin-right: 30px;
    }
}

.checkout_button {
    display: block;
    padding: 20px 50px;
    color: #8A8B90;
    font-weight: 400;
    font-size: 17px;
    background: #fff;
    border: 1px solid #B1B2B4;
    cursor: pointer;
    transition: 0.3s all ease;
}

.checkout_button:hover {
    color: #fff;
    background-color: rgb(153 149 149);
}


@media (min-width: 510px) {
    .checkout_button {
         padding: 20px 50px;
         font-size: 18px;
    }
}
