.cart_empty {
    margin-top: 50px;
    margin-bottom: 50px;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cart_empty img {
    width : 100%;
    obejct-fit : contain;
}

.cart_empty h1 {
    margin: 0;
    margin-top : 30px;
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: bold;
}

.cart_empty p {
    margin: 0;
    margin-bottom: 20px;
    font-weight: bold;
}
 
.cart_empty a {
    color: #3B3B3B;
    text-decoration: underline;
    cursor: pointer;
}
